<template>
    <transition name="body">
        <keep-alive>
            <router-view></router-view>
        </keep-alive>
    </transition>
</template>
<script>
    import {mapActions} from "vuex";

    export default {
        name: "app",
        data () {
            return {};
        },
        computed: {
            canShowHeader () {
                return this.$route.path == "/signin";
            }
        },
        methods: {
            handleBack () {
                this.$router.go(-1);
            },
            handleBrowserBack () {
                if (!this.$store.state.Global.isAllowBack) {
                    history.go(1);
                }
            },
            handleSignout () {
                //去除session
            },
            ...mapActions(["noPwdSignIn"])
        },
        created () {
            //第一次进入APP的时候,判断是否实现快速登录
            console.log(this.$route);
        },
        mounted () {
            //window.addEventListener("popstate", this.handleBrowserBack.bind(this));
        }
    };
</script>




